@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');
@import url(~antd/dist/antd.css);
/*
 * Converts a value (or list of values) into a standard scale with base 3px
 * Example:
 * get-unit(1) -> 3px
 * get-unit(2 1) -> 3px 6px
 * get-unit(4 2 4 1) -> 12px 6px 12px 3px
 */
/* get-color
 * Determines color and available shades
 * Each color has 9 levels and 5 is the default mid color one
*/
@keyframes color-orange-pulse {
  0% {
    background-color: #fea245;
  }
  50% {
    background-color: #ffeedd;
  }
  100% {
    background-color: #fea245;
  }
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.container {
  padding: 12px 50px;
  height: calc(100vh - 64px);
  overflow: auto;
}

.content {
  display: flex;
}

.section {
  display: flex;
}

.icon--green {
