.assignees-radio-group {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 8px;
}
.assignees-radio-group > label {
  width: 25%;
  text-align: center;
}
.assignees-radio-group > label:nth-child(1) {
  background: #fec891;
}
.assignees-radio-group > label:nth-child(2) {
