.header {
  display: flex;
  align-items: center;
}
.header__logo {
  height: 50px;
}
.header__nav ul {
  display: flex;
  list-style-type: none;
  padding: 0;
  margin-left: 20px;
}
.header__nav a {
  color: #00d8fa;
}
.header__nav li {
  display: inline-flex;
  margin: 0 12px;
}
.header__nav li .active {
  border-bottom: 3px solid white;
}

.footer {
  background: none;
  height: 64px;
  color: white;
  text-align: center;
  font-size: 75%;
}

.loading {
  display: flex;
  width: 100%;
  height: 100%;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.loading-logo__white {
  fill: #ffffff;
}

.loading-logo__cyan {
  fill: #00d8fa;
  animation: spiral-in-1 500ms ease-in-out infinite;
  transform-origin: center;
}

.loading-logo__blue {
  fill: #1890ff;
  animation: spiral-in-2 500ms ease-in-out infinite;
  transform-origin: center;
}

.loading-logo__black {
  fill: #000000;
  animation: spiral-in-3 500ms ease-in-out infinite;
  transform-origin: center;
}

@keyframes spiral-in-1 {
  0% {
    opacity: 0;
    transform: translateX(0) scale(1);
  }
  30% {
    transform: translateX(3%) scale(0.9);
  }
  50% {
    transform: translateX(-5%) scale(0.8);
  }
  75% {
    transform: translateX(0) scale(1);
    opacity: 1;
  }
}

@keyframes spiral-in-2 {
  0% {
    opacity: 0;
    transform: translateX(0) scale(1);
  }
  30% {
    transform: translateX(4%) scale(0.9);
  }
  50% {
    transform: translateX(-3%) scale(0.6);
  }
  75% {
    transform: translateX(0) scale(1);
    opacity: 1;
  }
}

@keyframes spiral-in-3 {
  0% {
    opacity: 0;
    transform: translateX(0) scale(1);
  }
  30% {
    transform: translateX(-5%) scale(0.9);
  }
  50% {
    transform: translateX(4%) scale(0.5);
  }
  75% {
    transform: translateX(0) scale(1);
    opacity: 1;
  }
}

.loading-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  padding: 24px;
  color: #6f7273;
}
.loading-container__message {
  text-align: center;
  font-style: italic;
}

.app {
  width: 100%;
  min-height: 100vh !important;
  background-color: #ecfafe;
}
.app--animated {
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #1890ff, #23d5ab, #ffffff);
  background-size: 600% 600%;
  animation: gradient 120s ease infinite;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.admin__form-container {
  width: 50%;
  border: 1px solid #d6d7d8;
  border-radius: 2px;
  background: rgba(255, 255, 255, 0.9);
  padding: 12px;
}

.admin-form__item {
  margin-bottom: 12px;
}

.admin-form__items {
  display: flex;
  justify-content: space-between;
}
.admin-form__items--2 .admin-form__item {
  width: 48%;
}
.admin-form__items--3 .admin-form__item {
  width: 30%;
}
.admin-form__items--4 .admin-form__item {
  width: 24%;
}
.admin-form__items--4 .admin-form__item--double {
