}

.home {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.home__logo-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.home__logo {
  width: auto;
  height: 40vh;
  display: block;
  animation: rotate 30s ease infinite;
}
.home__name {
  color: #ffffff;
  text-transform: uppercase;
  font-size: 300%;
  margin-bottom: 0;
}
.home__tagline {
  color: #ffffff;
  text-transform: uppercase;
}
.container .home {
  height: calc(100vh - 152px);
}

@keyframes rotate {
  0% {
    transform: rotate3d(0, 1, 0, 0deg);
  }
  90% {
    transform: rotate3d(0, 1, 0, 0deg);
  }
  95% {
    transform: rotate3d(0, 1, 0, 360deg);
  }
  100% {
    transform: rotate3d(0, 1, 0, 0deg);
  }
}

