.character {
  width: 600px;
  height: 600px;
  background: #fff;
  border: 2px solid #eee;
  position: relative;
}

.svg-sprite {
  position: absolute;
  width: 600px;
  height: 600px;
}

.svg-grid__base {
  fill: none;
  stroke: #ccc;
  stroke-miterlimit: 10;
}

.svg-grid__safety-lines {
  fill: none;
  stroke: #3cc;
  stroke-miterlimit: 10;
  stroke-width: 2;
}

.svg-grid__star-lines {
  fill: none;
  stroke: #ccc;
  stroke-miterlimit: 10;
  stroke-width: 3;
}

.svg-base-tail__border {
  stroke: #000000;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-miterlimit: 10;
  stroke-width: 6.4829;
  fill: #777470;
}

.svg-base-tail__fill {
  fill: #777470;
}

.svg-base-head__st1 {
  fill: none;
  stroke: #000000;
  stroke-width: 12.9659;
  stroke-linecap: round;
  stroke-linejoin: round;
}

.svg-base-head__st2 {
  fill: #777470;
}

.svg-base-head__snout {
  fill: #a3a19f;
}

.svg-base-head__nose,
.svg-base-head__pupil {
  fill: #3b3a36;
  stroke: #000000;
  stroke-width: 6.4829;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-miterlimit: 10;
}

.svg-base-head__eye {
  fill: #ffffff;
  stroke: #000000;
  stroke-width: 6.4829;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-miterlimit: 10;
}

.svg-base-body__st1 {
  fill: #777470;
  stroke: #000000;
  stroke-width: 6.4829;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-miterlimit: 10;
}

.svg-base-body__st2 {
  fill: #777470;
}

.svg-base-body__st3 {
  fill: #58595b;
  stroke: #000000;
  stroke-width: 6.4829;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-miterlimit: 10;
}

.color-swatch-radio.ant-radio-button-wrapper {
  padding: 0;
  height: auto;
}

.color-swatch {
  width: 25px;
  height: 25px;
  display: block;
}

.character-option {
  display: flex;
  flex-direction: column;
}

.options {
  margin: 0 0 8px 16px;
}

.button-container {
  display: flex;
  margin: 8px 0;
}
.button-container--tight > * {
  margin: 0 8px;
}
.button-container--wide > * {
  margin: 0 8px;
}
.button-container:first-child {
  margin-left: 0;
}
.button-container:last-child {
  margin-right: 0;
}
.button-container--left {
  justify-content: flex-start;
}
.button-container--center {
  justify-content: center;
}
.button-container--right {
  justify-content: flex-end;
}
.button-container--full {
  width: 100%;
}
.button-container--half {
  width: 50%;
}
.button-container--third {
  width: 33%;
}
.button-container--quarter {
  width: 25%;
}

.loading-checklist {
  max-width: 500px;
  margin: 12px auto;
}
.loading-checklist__title {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.loading-checklist__list {
  background: rgba(255, 255, 255, 0.3);
}

.member {
  display: flex;
  align-items: center;
  margin-bottom: 4px;
}
.member__separator {
  margin: 0 8px 0 4px;
}
.member__info {
  flex-grow: 1;
  display: flex;
  overflow-y: hidden;
}
.member__name {
  margin: 0 4px;
}
.member__position {
  text-transform: uppercase;
  font-size: 80%;
}
.member__progress {
  width: 50%;
  background: #d6d7d8;
  height: 10px;
  border-radius: 4px;
  display: flex;
}
.member__progress-bar {
  background: #959899;
  height: 10px;
  border-radius: 4px;
  transition: width 500ms;
}
.member__progress-number {
  margin-left: 8px;
  width: 36px;
}

.line-distribution__container {
  display: grid;
  width: 100%;
  margin: 0 auto 3em auto;
  height: 80vh;
  grid-template-columns: [start] 33.33% [middle] auto [end];
  grid-template-rows: [start] 33.33% [middle] auto [end];
  background-color: black;
}

.line-distribution__video {
  grid-column: start / middle;
  grid-row: start / middle;
  display: flex;
  justify-content: center;
  align-items: center;
}

.line-distribution__live-ranking {
  grid-column: start / middle;
  grid-row: middle / end;
  position: relative;
  padding: 12px;
  display: flex;
  flex-direction: column;
  background: #bdc3c7;
  background: linear-gradient(to bottom, #030303, #5f6e79);
}

.line-distribution__live-lyrics {
  grid-column: middle / end;
  grid-row: start / end;
  background: linear-gradient(to bottom, #c7d3db, #5f6e79);
  overflow-y: hidden;
}
.line-distribution__live-lyrics--results {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.line-distribution__live-lyrics h1 {
  text-align: center;
}

.rank-entry {
  width: 95%;
  height: 2.5em;
  margin-bottom: 0.25em;
  position: absolute;
  display: grid;
  opacity: 0.3;
  transition: opacity 500ms, transform 1s;
  grid-template-columns: [start] 2em [first] auto [second] 2.5em [end];
  grid-template-rows: [start] 1em [middle] auto [end];
  gap: 0.25em;
}
.rank-entry__avatar {
  grid-column: start / first;
  grid-row: start / end;
  background: white;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}
.rank-entry__name {
  grid-column: first / second;
  grid-row: start / middle;
  font-weight: bold;
  color: white;
  padding-left: 0.25em;
  line-height: 1em;
}
.rank-entry__progress {
  grid-column: first / second;
  grid-row: middle / end;
  display: flex;
  margin: 0 0.25em 0.25em 0.25em;
  background: rgba(255, 255, 255, 0.1);
  height: 0.5em;
  border-radius: 0.25em;
}
.rank-entry__progress-bar {
  height: 0.5em;
  border-radius: 0.25em;
  transition: width 500ms, background-color 1s;
}
.rank-entry__timestamp {
  grid-column: second / end;
  grid-row: start / end;
  justify-self: center;
  color: white;
}
.rank-entry--on {
  opacity: 1;
}
.rank-entry---1 {
  transform: translateY(24.75em);
  opacity: 0;
}
.rank-entry--0 {
  transform: translateY(0em);
}
.rank-entry--1 {
  transform: translateY(2.75em);
}
.rank-entry--2 {
  transform: translateY(5.5em);
}
.rank-entry--3 {
  transform: translateY(8.25em);
}
.rank-entry--4 {
  transform: translateY(11em);
}
.rank-entry--5 {
  transform: translateY(13.75em);
}
.rank-entry--6 {
  transform: translateY(16.5em);
}
.rank-entry--7 {
  transform: translateY(19.25em);
}
.rank-entry--8 {
  transform: translateY(22em);
}
.rank-entry--9 {
  transform: translateY(24.75em);
}

.lyrics-scroller__entries-container:first-child {
  transform: translateY(100vh);
}

.lyrics-scroller__entries-container:last-child {
  padding-bottom: 100vh;
}

.lyrics-scroller__li {
  transition: opacity 1s;
}
.lyrics-scroller__li--padded {
  opacity: 0.05;
}

.lyrics-scroller__title {
  background: white;
  width: 60%;
  text-align: center;
